<template>
<a-layout-content>
    <pre class="log_div">
        <p v-for="(item,index) in execute_log" :key="index"><span class="name">[{{item.name}}]</span> <span class="heng">-</span> <span>{{item.uuid}}</span> <span class="heng">-</span> <span class="appname">[{{item.app_name}}]</span> <span class="heng">-</span> <span class="appresult">{{item.result}}</span> <span class="heng">-</span> <span class="time">{{item.create_time.replace("T", " ")}}</span></p>
    </pre>
</a-layout-content>
</template>

<script>
export default {
    name: 'logsHome',
    data() {
        return {
            loading: false,
            execute_log: [],
        }
    },
    mounted() {
        this.onLoad();
    },
    methods: {
        onLoad() {
            this.loading = true;
            this.$http
                .post("/api/v1/get/logs/list")
                .then((res) => {
                    if (res.code == 0) {
                        this.execute_log = res.data;

                        setTimeout(function () {
                            var t = document.querySelector(".log_div");
                            t.scrollTop = t.scrollHeight;
                        }, 100);

                        this.loading = false;
                    } else {
                        this.$message.error(res.msg);
                        this.loading = false;
                    }
                });
        }
    },
}
</script>

<style lang="less" scoped>
@import url("../../theme/style.less");

.pointer {
    cursor: pointer;
}

.center {
    text-align: center;
}

.log_div {
    background: #151719;
    color: #bbb;
    height: calc(100vh - 13px);
    height: -moz-calc(100vh - 13px);
    height: -webkit-calc(100vh - 13px);
    margin: -10px;
    border-radius: 5px;
    padding-left: 30px;
    line-height: 15px;
    font-size: 14px;
    overflow-y: auto;
}

.blue,
.dark1 {
    .heng {
        color: #636363;
        margin-left: 15px;
        margin-right: 15px;
    }

    .name {
        color: #61b0fd;
    }

    .appresult {
        color: #ffffff;
    }
}

.dark {
    .heng {
        color: #636363;
        margin-left: 15px;
        margin-right: 15px;
    }

    .name {
        color: #d9a066;
    }

    .appresult {
        color: #ffffff;
    }
}
</style>



