<template>
  <a-layout-content>
    <div class="loginDiv">
      <div class="div">
        <div class="logos">
          <img :src="baseURL+'/app/basic/logo.png'"/>
          <p class="p1">织布鸟</p>
          <p class="p2">「安全编排与自动化响应平台」</p>
        </div>
        <div class="formDiv">
          <a-form :form="form" class="login-form" @submit="login">
            <a-form-item>
              <a-input v-decorator="[
                  'userName',
                  {
                    rules: [
                      { required: true, message: '账号(邮箱)不可为空' },
                      { min: 2, message: '账号(邮箱)长度不能少于2位' },
                      { max: 50, message: '账号(邮箱)长度不能超过50位' },
                    ],
                  },
                ]" placeholder="请输入账号名 or 邮箱">
                <a-icon slot="prefix" type="user" style="color: rgba(0, 0, 0, 0.25)"/>
              </a-input>
            </a-form-item>
            <a-form-item>
              <a-input-password v-decorator="[
                  'password',
                  {
                    rules: [
                      { required: true, message: '密码不可为空' },
                      { min: 8, message: '请输入8-16位密码' },
                      { max: 16, message: '请输入8-16位密码' },
                    ],
                  },
                ]" type="password" placeholder="请输入密码">
                <a-icon slot="prefix" type="lock" style="color: rgba(0, 0, 0, 0.25)"/>
              </a-input-password>
            </a-form-item>
            <a-form-item>
              <!-- <a class="login-form-forgot" @click="showUpdatePasswd">找回密码？</a> -->
              <a-button type="primary" html-type="submit" class="login-form-button" :loading="loading">登 录</a-button>
            </a-form-item>
          </a-form>
        </div>
        <div class="footer">
          <span>Copyright © 2020 ZBN.IO All rights reserved.</span>
        </div>
      </div>
    </div>
  </a-layout-content>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      form: this.$form.createForm(this),
      loading: false,
      curr_theme: "dark",
      baseURL: this.BaseURL,
    };
  },
  mounted() {
    this.getTheme();
  },
  methods: {
    getTheme() {
      var is_theme = this.$cookies.isKey("theme");
      if (is_theme) {
        var theme = this.$cookies.get("theme");

        if (theme == "dark1") {
          theme = "dark2";
        }

        document.getElementById("app").className = theme;
        this.curr_theme = theme;
      } else {
        document.getElementById("app").className = "dark";
        this.curr_theme = "dark";
      }
    },
    login(e) {
      e.preventDefault();

      this.form.validateFields((err, values) => {
        if (!err) {
          this.loading = true;

          this.$http
            .post("/api/v1/login", {
              account: values.userName,
              passwd: values.password,
            })
            .then((res) => {
              if (res.code == 0) {
                var token = res.data.token;
                var nick_name = res.data.nick_name;
                var account = res.data.account;
                var user_id = res.data.user_id;

                this.$cookies.set("token", token);
                this.$cookies.set("nick_name", nick_name);
                this.$cookies.set("account", account);
                this.$cookies.set("user_id", user_id);

                this.loading = false;
                window.location.href = "/";
              } else {
                this.$message.error(res.msg);
                this.loading = false;
              }
            }).catch((e) => {
            this.loading = false;
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import url("../theme/style.less");

.blue {
  .loginDiv {
    width: 100%;
    min-height: 100%;
    background: #fff url(../assets/background.svg) no-repeat 50%;
    background-size: 100%;
    padding: 50px 0 144px;
    position: relative;

    .div {
      width: 368px;
      margin: 0 auto;

      .logos {
        text-align: center;

        img {
          width: 150px;
        }

        .p1 {
          color: #333;
          font-size: 20px;
          margin: 0px;
          margin-top: 10px;
        }

        .p2 {
          color: #696969;
          font-size: 14px;
          margin: 0px;
          margin-top: 2px;
        }
      }

      .formDiv {
        margin-top: 30px;

        .login-form-button {
          width: 100%;
        }

        #components-form-demo-normal-login .login-form {
          max-width: 300px;
        }

        #components-form-demo-normal-login .login-form-forgot {
          float: right;
        }

        #components-form-demo-normal-login .login-form-button {
          width: 100%;
        }

        .login-form-forgot {
          float: right;
        }
      }

      .footer {
        text-align: center;
        color: #333;
      }
    }
  }
}

.dark,
.dark2 {
  .loginDiv {
    width: 100%;
    min-height: 100%;
    background-size: 100%;
    padding: 50px 0 144px;
    position: relative;

    .ant-btn-primary {
      background: @dark-nav-bg-color1;
      border-color: @dark-nav-bg-color1;
    }

    .div {
      width: 368px;
      margin: 0 auto;

      .logos {
        text-align: center;

        img {
          width: 150px;
        }

        .p1 {
          color: #fff;
          font-size: 20px;
          margin: 0px;
          margin-top: 10px;
        }

        .p2 {
          color: @jin-color;
          font-size: 14px;
          margin: 0px;
          margin-top: 2px;
        }
      }

      .formDiv {
        margin-top: 30px;

        .login-form-button {
          width: 100%;
        }

        #components-form-demo-normal-login .login-form {
          max-width: 300px;
        }

        #components-form-demo-normal-login .login-form-forgot {
          float: right;
        }

        #components-form-demo-normal-login .login-form-button {
          width: 100%;
        }

        .login-form-forgot {
          float: right;
        }
      }

      .footer {
        text-align: center;
        color: @dark-text-color;
      }
    }
  }
}
</style>



