<template>
  <div id="app">
    <a-layout class="layout" v-if="is_login == false">
      <my-nav/>
      <a-layout>
        <my-header/>
        <router-view/>
      </a-layout>
    </a-layout>
    <a-layout class="layout" v-else>
      <router-view/>
    </a-layout>
  </div>
</template>

<script>
import MyNav from "@/components/MyNav";
import MyHeader from "@/components/MyHeader";

export default {
  name: "App",
  components: {
    MyNav,
    MyHeader,
  },
  data() {
    return {
      is_login: true,
    };
  },
  created() {
    this.boolPage();
  },
  methods: {
    boolPage() {
      var key = this.$router.history.current.name;
      if (key == "Login") {
        if (this.isToken() == true) {
          this.is_login = true;
        } else {
          this.is_login = false;
        }
      } else {
        if (this.isToken() == true) {
          this.is_login = true;
        } else {
          this.is_login = false;
        }
      }
    },
    isToken() {
      var isToken = $cookies.isKey("token");

      if (isToken) {
        // var token = this.$cookies.get("token");
        return false;
      } else {
        return true;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import url("./theme/style.less");

body,
* {
  margin: 0px;
}

#app {
  @font-family: -apple-system,
  BlinkMacSystemFont,
  "Segoe UI",
  Roboto,
  "Helvetica Neue",
  Arial,
  "Noto Sans",
  sans-serif,
  "Apple Color Emoji",
  "Segoe UI Emoji",
  "Segoe UI Symbol",
  "Noto Color Emoji";
  height: 100%;
}

.blue,
.dark1 {
  .layout {
    height: 100%;

    .ant-layout-content {
      margin: 24px 16px;
      padding: 10px;
      background: @background;
      border-radius: 5px;
      min-height: auto;
      box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.05);
    }

    /deep/ .ant-collapse-borderless {
      background-color: #fff;
      border: 0;
      height: 100%;
      overflow: auto;
    }
  }
}

.dark,
.dark2 {
  .ant-layout {
    background: @dark-background1;
  }

  .layout {
    height: 100%;

    .ant-layout-content {
      margin: 24px 16px;
      padding: 10px;
      background: @dark-background;
      border-radius: 5px;
      min-height: auto;
      box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.05);
      color: @dark-text-color;
    }
  }

  /deep/ .ant-table {
    color: #e2e0e0;
  }

  /deep/ .ant-table-thead > tr > th {
    color: #e2e0e0;
    background: #202225;
    border-bottom: 1px solid #353535;
  }

  /deep/ .ant-table-tbody > tr > td {
    border-bottom: 1px solid #353535;
  }

  /deep/ .ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
    background: #292d38;
  }

  /deep/ .ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
    background: #292d38;
  }

  /deep/ .ant-pagination-prev .ant-pagination-item-link,
  .ant-pagination-next .ant-pagination-item-link {
    display: block;
    height: 100%;
    font-size: 12px;
    text-align: center;
    background-color: #202225;
    border: 1px solid #353535;
    border-radius: 4px;
    outline: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  /deep/ .ant-pagination-disabled a,
  .ant-pagination-disabled:hover a,
  .ant-pagination-disabled:focus a,
  .ant-pagination-disabled .ant-pagination-item-link,
  .ant-pagination-disabled:hover .ant-pagination-item-link,
  .ant-pagination-disabled:focus .ant-pagination-item-link {
    color: #ffffff61;
  }

  /deep/ .ant-pagination-next a {
    color: #ffffff61;
  }

  /deep/ .ant-pagination-prev a,
  .ant-pagination-next a {
    color: #ffffff61;
  }

  /deep/ .ant-pagination-item-link {
    display: block;
    height: 100%;
    font-size: 12px;
    text-align: center;
    background-color: #202225;
    border: 1px solid #353535;
    border-radius: 4px;
    outline: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  /deep/ .ant-pagination-item-active {
    font-weight: 500;
    background: #202225;
    border-color: #353535;
  }

  /deep/ .ant-pagination-item {
    background: #202225;
    border-color: #353535;
  }

  /deep/ .ant-pagination-item a {
    display: block;
    padding: 0 6px;
    color: #a7a7a7a6;
    -webkit-transition: none;
    transition: none;
  }

  /deep/ .ant-pagination-item-active a {
    color: #fff;
  }

  /deep/ .ant-btn-primary {
    color: #fff;
    background-color: #bd854d;
    border-color: #bd854d;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
    -webkit-box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
  }

  /deep/ .ant-table-placeholder {
    position: relative;
    z-index: 1;
    margin-top: -1px;
    padding: 16px 16px;
    color: rgba(0, 0, 0, 0.25);
    font-size: 14px;
    text-align: center;
    background: #27292e;
    border-top: 1px solid #353535;
    border-bottom: 1px solid #353535;
    border-radius: 0 0 4px 4px;
  }

  /deep/ .ant-empty-normal {
    margin: 32px 0;
    color: #ccc;
  }

  /deep/ .ant-collapse > .ant-collapse-item > .ant-collapse-header {
    color: #e4e4e4;
  }

  /deep/ .ant-collapse-borderless {
    background-color: #27292e;
    border: 0;
    height: 100%;
    overflow: auto;
  }

  /deep/ .ant-collapse-borderless > .ant-collapse-item {
    border-bottom: 1px solid #353535;
  }

  /deep/ .ant-collapse-borderless > .ant-collapse-item > .ant-collapse-content > .ant-collapse-content-box {
    padding-top: 16px;
  }
}
</style>



