<template>
<a-layout-content>
    <div class="header_div">
        <a-select default-value="0" style="width: 120px" @change="onSelect" class="align">
            <a-select-option value="0">全部</a-select-option>
            <a-select-option v-for="(item, index) in type_data" :key="index" :value="item.id">{{ item.name }}</a-select-option>
        </a-select>
        <a-input-search placeholder="请输入剧本名称" style="width: 200px" @search="onSearch" class="align" />
        <a-button type="primary" icon="plus-circle" class="align btn_add" @click="tzAddPlayBook">
            Playbook
        </a-button>
    </div>
    <a-table rowKey="id" :columns="columns" :data-source="data" :loading="loading">
        <a-tag slot="type_name" slot-scope="text" class="color1">
            {{ text }}
        </a-tag>

        <span slot="name" slot-scope="text">
            <b>{{ text }}</b>
        </span>

        <span slot="update_time" slot-scope="text">
            {{ text.replace("T", " ") }}
        </span>

        <span slot="create_time" slot-scope="text">
            {{ text.replace("T", " ") }}
        </span>

        <span slot="action" slot-scope="text, record">
            <a-popconfirm title="是否要删除该剧本?" ok-text="是" cancel-text="否" @confirm="del(record.uuid)">
                <a-tag color="#e45f5f" class="pointer"> 删除 </a-tag>
            </a-popconfirm>

            <a-tag color="#2db7f5" class="pointer" @click="tzUpdatePlayBook(record.uuid)">
                编辑
            </a-tag>
        </span>
    </a-table>
</a-layout-content>
</template>

<script>
const columns = [{
        title: "分类",
        dataIndex: "type_name",
        key: "type_name",
        scopedSlots: {
            customRender: "type_name",
        },
    }, {
        title: "剧本名称",
        dataIndex: "name",
        key: "name",
        scopedSlots: {
            customRender: "name",
        },
    },
    {
        title: "创建人",
        dataIndex: "nick_name",
        key: "nick_name",
    },
    {
        title: "更新时间",
        key: "update_time",
        dataIndex: "update_time",
        scopedSlots: {
            customRender: "update_time",
        },
    },
    {
        title: "创建时间",
        key: "create_time",
        dataIndex: "create_time",
        scopedSlots: {
            customRender: "create_time",
        },
    },
    {
        title: "操作",
        key: "action",
        scopedSlots: {
            customRender: "action",
        },
        width: 140,
    }
];

export default {
    name: "workflowHome",
    data() {
        return {
            columns,
            loading: false,
            data: [],
            so_text: "",
            select_type: "0",
            type_data: []
        };
    },
    mounted() {
        this.onLoad();
    },
    methods: {
        onLoad(keywords = "", type = "0") {
            this.loading = true;
            this.onSelectType();
            this.$http
                .post("/api/v1/get/workflow/list", {
                    keywords: keywords,
                    type: type
                })
                .then((res) => {
                    if (res.code == 0) {
                        this.data = res.data;
                        this.loading = false;
                    } else {
                        this.$message.error(res.msg);
                        this.loading = false;
                    }
                });
        },
        onSelectType() {
            this.$http
                .post("/api/v1/get/type/list", {
                    type: 1,
                })
                .then((res) => {
                    if (res.code == 0) {
                        this.type_data = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        del(uuid) {
            this.$http
                .post("/api/v1/post/workflow/del", {
                    uuid: uuid,
                })
                .then((res) => {
                    if (res.code == 0) {
                        this.$message.success("删除成功");
                        this.onLoad();
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onSearch(value) {
            this.so_text = value;
            this.onLoad(this.so_text, this.select_type);
        },
        onSelect(value) {
            this.select_type = value;
            this.onLoad(this.so_text, this.select_type);
        },
        tzAddPlayBook() {
            this.$http
                .post("/api/v1/post/workflow/add")
                .then((res) => {
                    if (res.code == 0) {
                        let uuid = res.data.uuid;
                        this.$router.push({
                            name: "WorkflowEdit",
                            params: {
                                uuid: uuid
                            }
                        });
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        tzUpdatePlayBook(uuid) {
            this.$router.push({
                name: "WorkflowEdit",
                params: {
                    uuid: uuid
                }
            });
        }
    },
};
</script>

<style lang="less" scoped>
@import url("../../theme/style.less");

.pointer {
    cursor: pointer;
}

.center {
    text-align: center;
}

.blue,
.dark1 {
    .header_div {
        width: 100%;
        background: #fafafa;
        margin-bottom: 10px;
        padding: 10px;
        border: 1px solid #f5f5f5;
        border-radius: 5px;

        .align {
            vertical-align: middle;
        }

        .btn_add {
            float: right;
        }
    }
}

.blue {
    .color1 {
        background: #03396d;
        color: #fff;
        border: 1px solid #03396d;
    }
}

.dark1 {
    .color1 {
        background: #bd854d;
        color: #fff;
        border: 1px solid #bd854d;
    }
}

.dark {
    .header_div {
        width: 100%;
        background: #202225;
        margin-bottom: 10px;
        padding: 10px;
        border: 1px solid #353535;
        border-radius: 5px;

        .align {
            vertical-align: middle;
        }

        .btn_add {
            float: right;
        }
    }

    .color1 {
        background: #bd854d;
        color: #fff;
        border: 1px solid #bd854d;
    }
}
</style>



