<template>
  <a-layout-content>
    <div class="header_div">
      <a-select
        default-value="0"
        style="width: 120px"
        @change="onSelect"
        class="align"
      >
        <a-select-option value="0">全部</a-select-option>
        <a-select-option
          v-for="(item, index) in type_data"
          :key="index"
          :value="item.id"
          >{{ item.name }}</a-select-option
        >
      </a-select>
      <a-input-search
        placeholder="请输入名称"
        style="width: 200px"
        @search="onSearch"
        class="align"
      />
      <a-button
        type="primary"
        icon="plus-circle"
        class="align btn_add"
        @click="showAdd"
      >
        添加
      </a-button>
    </div>
    <a-table
      rowKey="id"
      :columns="columns"
      :data-source="data"
      :loading="loading"
    >
      <span slot="key" slot-scope="text">
        <b>{{ text }}</b>
      </span>

      <a-tag slot="type_name" slot-scope="text" class="color1">
        {{ text }}
      </a-tag>

      <span slot="update_time" slot-scope="text">
        {{ text.replace("T", " ") }}
      </span>

      <span slot="create_time" slot-scope="text">
        {{ text.replace("T", " ") }}
      </span>

      <span slot="action" slot-scope="text, record">
        <a-popconfirm
          title="是否要删除该变量?"
          ok-text="是"
          cancel-text="否"
          @confirm="del(record.id)"
        >
          <a-tag color="#e45f5f" class="pointer"> 删除 </a-tag>
        </a-popconfirm>

        <a-tag
          color="#2db7f5"
          class="pointer"
          @click="
            showEdit(record.id, record.type_id, record.key, record.value)
          "
        >
          编辑
        </a-tag>
      </span>
    </a-table>

    <a-drawer
      title="添加新变量"
      :width="300"
      :visible="visible_add"
      :body-style="{ paddingBottom: '80px' }"
      @close="onCloseAdd"
    >
      <a-form :form="formAdd" layout="vertical" @submit="onAdd">
        <a-row :gutter="16">
          <a-col :span="24">
            <a-form-item label="分类">
              <a-select
                v-decorator="[
                  'type',
                  {
                    rules: [{ required: true, message: '请选择分类' }],
                  },
                ]"
                placeholder="请选择分类"
              >
                <a-select-option
                  v-for="(item, index) in type_data"
                  :key="index"
                  :value="item.id"
                  >{{ item.name }}</a-select-option
                >
              </a-select>
            </a-form-item>
          </a-col>

          <a-col :span="24">
            <a-form-item label="Key">
              <a-input
                v-decorator="[
                  'keys',
                  {
                    rules: [
                      { required: true, message: '请输入变量 Key' },
                      { max: 20, message: 'Key 长度不能超过20位' },
                    ],
                  },
                ]"
                placeholder="请输入分类名称"
              />
            </a-form-item>
          </a-col>

          <a-col :span="24">
            <a-form-item label="Value">
              <a-input
                v-decorator="[
                  'values',
                  {
                    rules: [
                      { required: true, message: '请输入变量 Value' },
                      { max: 255, message: 'Value 长度不能超过255位' },
                    ],
                  },
                ]"
                placeholder="请输入分类名称"
              />
            </a-form-item>
          </a-col>
        </a-row>

        <div
          :style="{
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: '100%',
            borderTop: '1px solid #e9e9e9',
            padding: '10px 16px',
            background: '#fff',
            textAlign: 'right',
            zIndex: 1,
          }"
        >
          <a-button :style="{ marginRight: '8px' }" @click="onCloseAdd"
            >关闭</a-button
          >
          <a-button type="primary" html-type="submit" :loading="loadingAdd">
            添加
          </a-button>
        </div>
      </a-form>
    </a-drawer>

    <a-drawer
      title="更新变量信息"
      :width="300"
      :visible="visible_edit"
      :body-style="{ paddingBottom: '80px' }"
      @close="onCloseEdit"
    >
      <a-form :form="formUpdate" layout="vertical" @submit="onUpdate">
        <a-row :gutter="16">
          <a-col :span="24">
            <a-form-item label="分类">
              <a-select
                v-decorator="[
                  'type',
                  {
                    rules: [{ required: true, message: '请选择分类' }],
                  },
                ]"
                placeholder="请选择分类"
              >
                <a-select-option
                  v-for="(item, index) in type_data"
                  :key="index"
                  :value="String(item.id)"
                  >{{ item.name }}</a-select-option
                >
              </a-select>
            </a-form-item>
          </a-col>

          <a-col :span="24">
            <a-form-item label="Key">
              <a-input
                v-decorator="[
                  'keys',
                  {
                    rules: [
                      { required: true, message: '请输入变量 Key' },
                      { max: 20, message: 'Key 长度不能超过20位' },
                    ],
                  },
                ]"
                placeholder="请输入分类名称"
              />
            </a-form-item>
          </a-col>

          <a-col :span="24">
            <a-form-item label="Value">
              <a-input
                v-decorator="[
                  'values',
                  {
                    rules: [
                      { required: true, message: '请输入变量 Value' },
                      { max: 255, message: 'Value 长度不能超过255位' },
                    ],
                  },
                ]"
                placeholder="请输入分类名称"
              />
            </a-form-item>
          </a-col>
        </a-row>

        <div
          :style="{
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: '100%',
            borderTop: '1px solid #e9e9e9',
            padding: '10px 16px',
            background: '#fff',
            textAlign: 'right',
            zIndex: 1,
          }"
        >
          <a-button :style="{ marginRight: '8px' }" @click="onCloseEdit"
            >关闭</a-button
          >
          <a-button type="primary" html-type="submit" :loading="loadingUpdate">
            更新
          </a-button>
        </div>
      </a-form>
    </a-drawer>
  </a-layout-content>
</template>

<script>
const columns = [
  {
    title: "ID",
    dataIndex: "id",
    key: "id",
  },
  {
    title: "分类",
    dataIndex: "type_name",
    key: "type_name",
    scopedSlots: {
      customRender: "type_name",
    },
  },
  {
    title: "Key",
    dataIndex: "key",
    key: "key",
    scopedSlots: {
      customRender: "key",
    },
  },
  {
    title: "Value",
    dataIndex: "value",
    key: "value",
    scopedSlots: {
      customRender: "value",
    },
  },
  {
    title: "更新时间",
    key: "update_time",
    dataIndex: "update_time",
    scopedSlots: {
      customRender: "update_time",
    },
  },
  {
    title: "创建时间",
    key: "create_time",
    dataIndex: "create_time",
    scopedSlots: {
      customRender: "create_time",
    },
  },
  {
    title: "操作",
    key: "action",
    scopedSlots: {
      customRender: "action",
    },
    width: 140,
  },
];

export default {
  name: "variableHome",
  data() {
    return {
      columns,
      loading: false,
      data: [],
      type_data: [],
      formAdd: this.$form.createForm(this),
      visible_add: false,
      formUpdate: this.$form.createForm(this),
      visible_edit: false,
      key: 0,
      loadingUpdate: false,
      loadingAdd: false,
      so_text: "",
      select_type: 0,
    };
  },
  mounted() {
    this.onLoad();
  },
  methods: {
    onLoad(keywords = "", type = 0) {
      this.loading = true;
      this.$http
        .post("/api/v1/get/variablen/list", {
          type: type,
          keywords: keywords,
        })
        .then((res) => {
          if (res.code == 0) {
            this.data = res.data;
            this.onSelectType();
            this.loading = false;
          } else {
            this.$message.error(res.msg);
            this.loading = false;
          }
        });
    },
    onSelectType() {
      this.$http
        .post("/api/v1/get/type/list", {
          type: 2,
        })
        .then((res) => {
          if (res.code == 0) {
            this.type_data = res.data;
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    del(id) {
      this.$http
        .post("/api/v1/post/variablen/del", {
          id: id,
        })
        .then((res) => {
          if (res.code == 0) {
            this.$message.success("删除成功");
            this.onLoad();
          } else {
            this.$message.error(res.msg);
          }
        });
    },
    onAdd(e) {
      e.preventDefault();

      this.formAdd.validateFields((err, values) => {
        if (!err) {
          this.loadingAdd = true;

          this.$http
            .post("/api/v1/post/variablen/add", {
              type_id: values.type,
              key: values.keys,
              value: values.values,
            })
            .then((res) => {
              if (res.code == 0) {
                this.$message.success("添加成功");
                this.loadingAdd = false;
                this.onCloseAdd();
                this.onLoad();
              } else {
                this.$message.error(res.msg);
                this.loadingAdd = false;
              }
            });
        }
      });
    },
    onUpdate(e) {
      e.preventDefault();

      this.formUpdate.validateFields((err, values) => {
        if (!err) {
          this.loadingUpdate = true;

          this.$http
            .post("/api/v1/post/variablen/update", {
              id: this.key,
              type_id: values.type,
              key: values.keys,
              value: values.values,
            })
            .then((res) => {
              if (res.code == 0) {
                this.$message.success("更新成功");
                this.loadingUpdate = false;
                this.onCloseEdit();
                this.onLoad();
              } else {
                this.$message.error(res.msg);
                this.loadingUpdate = false;
              }
            });
        }
      });
    },
    showEdit(key, type_id, keys, values) {
      setTimeout(() => {
        this.formUpdate.setFieldsValue({
          type: String(type_id),
          keys: keys,
          values: values,
        });
      }, 100);

      this.key = key;

      this.visible_edit = true;
    },
    onCloseEdit() {
      this.visible_edit = false;
    },
    showAdd() {
      this.visible_add = true;
    },
    onCloseAdd() {
      this.visible_add = false;
    },
    onSearch(value) {
      this.so_text = value;
      this.onLoad(this.so_text, this.select_type);
    },
    onSelect(value) {
      this.select_type = value;
      this.onLoad(this.so_text, this.select_type);
    },
  },
};
</script>

<style lang="less" scoped>
@import url("../../theme/style.less");

.pointer {
  cursor: pointer;
}

.center {
  text-align: center;
}

.blue,
.dark1 {
  .header_div {
    width: 100%;
    background: #fafafa;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #f5f5f5;
    border-radius: 5px;

    .align {
      vertical-align: middle;
    }

    .btn_add {
      float: right;
    }
  }
}

.blue {
  .color1 {
    background: #03396d;
    color: #fff;
    border: 1px solid #03396d;
  }
}
.dark1 {
  .color1 {
    background: #bd854d;
    color: #fff;
    border: 1px solid #bd854d;
  }
}

.dark {
  .header_div {
    width: 100%;
    background: #202225;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #353535;
    border-radius: 5px;

    .align {
      vertical-align: middle;
    }

    .btn_add {
      float: right;
    }
  }

  .color1 {
    background: #bd854d;
    color: #fff;
    border: 1px solid #bd854d;
  }
}
</style>



