<template>
<div>
    <a-row type="flex">
        <a-col flex="180px">
            <div class="leftx">
                <a-collapse accordion default-active-key="1" :bordered="false" expand-icon-position="right">
                    <template #expandIcon="props">
                        <a-icon type="caret-right" :rotate="props.isActive ? 90 : 0" />
                    </template>
                    <a-collapse-panel key="1" header="控制器">
                        <a-row :gutter="[16,16]">
                            <a-col :span="12">
                                <div class="tools_div center" draggable="true" @dragend="onDragendAddNode($event,baseURL+'/app/basic/start.jpg','开始','start')">
                                    <a-avatar :size="55" :src="baseURL+'/app/basic/start.jpg'" />
                                    <div class="tools_name"><span>开始</span></div>
                                </div>
                            </a-col>

                            <a-col :span="12">
                                <div class="tools_div center" draggable="true" @dragend="onDragendAddNode($event,baseURL+'/app/basic/end.jpg','结束','end')">
                                    <a-avatar :size="55" :src="baseURL+'/app/basic/end.jpg'" />
                                    <div class="tools_name"><span>结束</span></div>
                                </div>
                            </a-col>
                        </a-row>
                    </a-collapse-panel>
                    <a-collapse-panel key="2" header="应用中心">
                        <a-row :gutter="[16,16]">
                            <a-col :span="12" v-for="(item,index) in app_list" :key="index">
                                <div class="tools_div center" draggable="true" @dragend="onDragendAddNode($event,baseURL+'/app/'+item.icon,item.name,item.app_dir)">
                                    <a-avatar :size="55" :src="baseURL+'/app/'+item.icon" />
                                    <div class="tools_name"><span>{{item.name}}</span></div>
                                </div>
                            </a-col>
                        </a-row>
                    </a-collapse-panel>
                    <!-- <a-collapse-panel key="3" header="通知中心">
                        <a-row :gutter="[16,16]">
                            <a-col :span="12" v-for="(item,index) in app_list" :key="index">
                                <div class="tools_div center">
                                    <a-avatar :size="55" :src="baseURL+'/app/'+item.icon" />
                                    <div class="tools_name"><span>{{item.name}}</span></div>
                                </div>
                            </a-col>
                        </a-row>
                    </a-collapse-panel> -->
                </a-collapse>
            </div>
        </a-col>
        <a-col flex="auto">
            <div class="edit">
                <a-tooltip placement="top">
                    <template slot="title">
                        <span>执行剧本</span>
                    </template>
                    <img class="btn_run" @click="onRun" src="data:image/png;base64," alt="">
                </a-tooltip>
                <a-select v-model="select_type" style="width: 120px" @change="onSelect" class="align">
                    <a-select-option v-for="(item, index) in type_data" :key="index" :value="String(item.id)">{{ item.name }}</a-select-option>
                </a-select>
                <a-input class="work_title" placeholder="请输入剧本名称" v-model="workflow_name" />
                <div id="container"></div>
            </div>
        </a-col>
    </a-row>

    <a-drawer class="visible_log" :closable="false" :width="500" :maskClosable="false" :visible="visible_log" @close="onCloseLog">
        <div>
            <a-icon class="pointer" type="close-circle" @click="onCloseLog" />
        </div>
        <pre class="log_div">
            <p v-for="(item,index) in execute_log" :key="index"><span class="appname">[{{item.app_name}}]</span> - <span class="appresult">{{item.result}}</span> - <span class="time">{{item.create_time.replace("T", " ")}}</span></p>
        </pre>
    </a-drawer>

    <a-drawer title="节点编辑" :width="500" :visible="visible_edit" :body-style="{ paddingBottom: '80px' }" @close="onClose">
        <a-form ref="editForm" :form="form" layout="vertical" @submit="onUpdate">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-form-item label="节点名称">
                        <a-input v-decorator="[
                  'node_name',
                  {
                    rules: [
                      { required: true, message: '请输入节点名称' },
                      { max: 20, message: '节点名称长度不能超过20位' },
                    ],
                  },
                ]" placeholder="请输入节点名称" />
                    </a-form-item>
                </a-col>

                <a-col :span="24">
                    <a-form-item>
                        <span slot="label">
                            动作&nbsp;
                            <a-tooltip title="使用说明">
                                <a-icon class="pointer" @click="onShowMd" type="question-circle-o" />
                            </a-tooltip>
                        </span>
                        <a-select v-decorator="[
                  'action',
                  {
                    rules: [{ required: true, message: '请选择执行动作' }],
                  },
                ]" placeholder="请选择执行动作" @change="selectAppAction">
                            <a-select-option v-for="(item, index) in app_info.action" :key="index" :value="item.func">{{ item.name }}</a-select-option>
                        </a-select>
                    </a-form-item>
                </a-col>

                <a-col :span="24" v-for="(item, index) in app_info_args" :key="index">
                    <a-form-item :label="item.key">
                        <a-input v-decorator="[
                  item.key,
                  {
                    rules: [
                      { required: item.required, message: '请输入 '+item.key+' 参数的内容' }
                    ],
                  },
                ]" :placeholder="'请输入 '+item.key+' 参数的内容'" />
                    </a-form-item>
                </a-col>
            </a-row>

            <a-drawer :title="app_title" :width="600" :visible="visible_md" :body-style="{ paddingBottom: '80px' }" @close="onCloseMd">
                <div class="markdown-body" v-html="html"></div>
            </a-drawer>

            <div :style="{
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: '100%',
            borderTop: '1px solid #e9e9e9',
            padding: '10px 16px',
            background: '#fff',
            textAlign: 'right',
            zIndex: 1,
          }">
                <a-button :style="{ marginRight: '8px' }" @click="onClose">关闭</a-button>
                <a-button type="primary" html-type="submit">
                    保存
                </a-button>
            </div>
        </a-form>
    </a-drawer>

    <a-drawer title="链路编辑" :width="300" :visible="visible_edit_edge" :body-style="{ paddingBottom: '80px' }" @close="onCloseEdge">
        <a-form ref="editForm" :form="form_edge" layout="vertical" @submit="onUpdateEdge">
            <a-row :gutter="16">
                <a-col :span="24">
                    <a-form-item label="相等条件判断">
                        <a-input v-decorator="[
                  'ifelse',
                  {
                    rules: [
                      { max: 100, message: '条件长度不能超过100位' },
                    ],
                  },
                ]" placeholder="请输入条件" />
                    </a-form-item>
                </a-col>
            </a-row>

            <div :style="{
            position: 'absolute',
            right: 0,
            bottom: 0,
            width: '100%',
            borderTop: '1px solid #e9e9e9',
            padding: '10px 16px',
            background: '#fff',
            textAlign: 'right',
            zIndex: 1,
          }">
                <a-button :style="{ marginRight: '8px' }" @click="onCloseEdge">关闭</a-button>
                <a-button type="primary" html-type="submit">
                    保存
                </a-button>
            </div>
        </a-form>
    </a-drawer>
</div>
</template>

<script>
import G6 from "@antv/g6";
import insertCss from 'insert-css';
import {
    uuid
} from 'vue-uuid';
import marked from 'marked';
import 'github-markdown-css/github-markdown.css'

export default {
    name: "workflowEdit",
    data() {
        return {
            app_title: "",
            visible_md: false,
            html: "",
            uuid: "",
            baseURL: this.BaseURL,
            app_list: [],
            graph: "",
            childNode: "",
            data: {
                nodes: [],
                edges: []
            },
            app_data: {},
            node_color: "",
            edge_color: "",
            plug_minimap: null,
            plug_toolbar: null,
            plug_contextMenu: null,
            visible_edit: false,
            visible_log: false,
            visible_edit_edge: false,
            form: this.$form.createForm(this),
            form_edge: this.$form.createForm(this),
            key: null,
            app_info: {},
            app_info_args: [],
            workflow_name: "",
            is_mounted: 0,
            start_app: "",
            end_app: "",
            websock: null,
            ping_timetimer: null,
            execute_log: [],
            select_type: "",
            type_data: []
        };
    },
    mounted() {
        this.is_mounted++;
        this.uuid = this.$route.params.uuid;
        this.$store.commit("closeCollapsed");
        this.onLoad();
    },
    methods: {
        onLoad() {
            this.onColor();
            this.onAppList();
            this.onSelectType();
            this.onData();
            this.initWebSocket();
        },
        onShowMd() {
            let app_info = this.app_data[this.key];

            let md_url = "/app/" + app_info["app_dir"] + "/readme.md";

            this.$http
                .get(md_url)
                .then((res) => {
                    this.app_title = app_info["name"] + " v" + app_info["version"];

                    marked.setOptions({
                        gfm: true,
                        tables: true,
                        breaks: true,
                        smartLists: true
                    });

                    this.html = marked(res);
                    this.visible_md = true;
                });
        },
        onCloseMd() {
            this.visible_md = false;
        },
        onSelect(value) {
            this.select_type = value;
            this.onSave();
        },
        onSelectType() {
            this.$http
                .post("/api/v1/get/type/list", {
                    type: 1,
                })
                .then((res) => {
                    if (res.code == 0) {
                        this.type_data = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onSave() {
            this.data = this.graph.save();

            // 更新剧本信息
            this.$http
                .post("/api/v1/post/workflow/update", {
                    uuid: this.uuid,
                    name: this.workflow_name,
                    start_app: this.start_app,
                    end_app: this.end_app,
                    flow_json: JSON.stringify(this.data),
                    flow_data: JSON.stringify(this.app_data),
                    type_id: this.select_type
                })
                .then((res) => {
                    if (res.code == 0) {} else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onRun() {
            this.data = this.graph.save();

            let uuid_list = new Set()

            for (let i in this.data.edges) {
                uuid_list.add(this.data.edges[i].source)
                uuid_list.add(this.data.edges[i].target)
            }

            for (let i of uuid_list) {
                console.log(this.app_data[i]);
                if (typeof this.app_data[i] != "undefined") {
                    if (this.app_data[i].hasOwnProperty("data") == false) {
                        this.$message.warning("请选择 APP 动作");
                        return false;
                    }
                }
            }

            this.onShowLog();

            // 更新剧本信息
            this.$http
                .post("/api/v1/post/workflow/update", {
                    uuid: this.uuid,
                    name: this.workflow_name,
                    start_app: this.start_app,
                    end_app: this.end_app,
                    flow_json: JSON.stringify(this.data),
                    flow_data: JSON.stringify(this.app_data),
                    type_id: this.select_type
                })
                .then((res) => {
                    if (res.code == 0) {
                        let msg = JSON.stringify({
                            "method": "run",
                            "data": {
                                "uuid": this.uuid
                            }
                        })

                        this.sendMSG(msg)
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onData() {
            // 加载剧本信息
            this.$http
                .post("/api/v1/post/workflow/detail", {
                    uuid: this.uuid
                })
                .then((res) => {
                    if (res.code == 0) {
                        this.select_type = String(res.data.type_id);
                        this.workflow_name = res.data.name;

                        if (res.data.flow_json === "") {
                            this.data = {
                                nodes: [],
                                edges: []
                            }
                        } else {
                            this.data = JSON.parse(res.data.flow_json);
                        }

                        if (res.data.flow_data === "") {
                            this.app_data = {}
                        } else {
                            this.app_data = JSON.parse(res.data.flow_data);
                        }

                        this.start_app = res.data.start_app;
                        this.end_app = res.data.end_app;

                        this.onFlowPlug();
                        this.onFlowChart();
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onAppList() {
            // 加载 APP 列表
            this.$http
                .get("/api/v1/get/app/list")
                .then((res) => {
                    if (res.code == 0) {
                        this.app_list = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onThemeCss() {
            insertCss(`
                .g6-minimap-container {
                    border: 1px solid #606067;
                }

                .g6-minimap-viewport {
                    border: 2px solid #000;
                }

                .g6-minimap {
                    position: fixed;
                    right: 10px;
                    bottom: 10px;
                }

                .g6-component-toolbar li {
                    list-style-type: none !important;
                }
                
                .g6-component-contextmenu {
                    position: absolute;
                    list-style-type: none;
                    padding: 10px 8px;
                    left: -150px;
                    background-color: rgba(255, 255, 255, 0.9);
                    border: 1px solid #e2e2e2;
                    border-radius: 4px;
                    font-size: 12px;
                    color: #3c3c3c;
                }

                .g6-component-contextmenu ul {
                    padding-left: 0px;
                    line-height: 30px;
                    margin: 0px;
                }

                .g6-component-contextmenu li {
                    cursor: pointer;
                    list-style-type:none;
                    list-style: none;
                    margin-left: 0px;
                }

                .g6-component-contextmenu li:hover {
                    color: #a76f36;
                }

                .g6-component-toolbar {
                    position: absolute;
                    list-style-type: none;
                    padding: 6px;
                    left: 10px;
                    top: inherit;
                    bottom: 10px;
                    background-color: rgba(255, 255, 255, 0.9);
                    border: 1px solid #e2e2e2;
                    border-radius: 4px;
                    font-size: 12px;
                    color: #545454;
                    margin: 0;
                }
            `);
        },
        onColor() {
            var theme = this.$store.getters.getTheme;

            if (theme == "blue" || theme == "dark1") {
                this.node_color = "#000000";
                this.edge_color = "#000000";
            } else if (theme == "dark") {
                this.node_color = "#FFFFFF";
                this.edge_color = "#F6BD16";
            }
        },
        onFlowPlug() {
            this.onThemeCss();

            // 小地图
            this.plug_minimap = new G6.Minimap({
                size: [150, 100],
            });

            // 工具
            this.plug_toolbar = new G6.ToolBar();

            // 右键菜单
            this.plug_contextMenu = new G6.Menu({
                getContent(graph) {
                    return `<ul>
                <li>编辑</li>
                <li>删除</li>
                </ul>`;
                },
                handleMenuClick: (target, item) => {
                    let action = target.innerHTML;

                    let type = item._cfg.type;
                    let name = item._cfg.model.label;
                    let id = item._cfg.id;

                    if (action == "编辑") {
                        if (id == this.start_app) {
                            this.$message.warning("开始节点不可使用编辑功能");
                            return false;
                        } else if (id == this.end_app) {
                            this.$message.warning("结束节点不可使用编辑功能");
                            return false;
                        }

                        this.onShow(id, name, type);
                    } else if (action == "删除") {
                        this.graph.removeItem(item);
                    }
                },
                // 需要加上父级容器的 padding-left 16 与自身偏移量 10
                offsetX: 0,
                // 需要加上父级容器的 padding-top 24 、画布兄弟元素高度、与自身偏移量 10
                offsetY: 0,
                // 在哪些类型的元素上响应
                itemTypes: ["node", "edge"],
            });
        },
        onDragendAddNode(event, icon, name, app_dir) {
            // 坐标转换
            let point = this.graph.getPointByClient(event.clientX, event.clientY)

            let node_id = uuid.v1();

            if (name == "开始") {
                if (this.start_app != "") {
                    this.$message.warning("只能存在一个开始节点");
                    return false;
                }
                this.start_app = node_id;
            } else if (name == "结束") {
                if (this.end_app != "") {
                    this.$message.warning("只能存在一个结束节点");
                    return false;
                }
                this.end_app = node_id;
            }

            let new_node = {
                id: node_id,
                label: name,
                img: icon,
                x: point.x,
                y: point.y
            };

            this.graph.addItem('node', new_node);

            this.data.nodes = this.graph.save().nodes;

            this.app_data[node_id] = this.app_list[app_dir]

            this.onTheme();
            this.onSave();
        },
        onFlowChart() {
            // 初始化流程图
            const width = document.getElementById("container").scrollWidth;
            const height = document.getElementById("container").scrollHeight;

            this.graph = new G6.Graph({
                container: "container",
                width: width,
                height: height,
                fitCenter: true,
                defaultNode: {
                    type: "image",
                    size: 60,
                    clipCfg: {
                        show: true,
                        type: 'circle',
                        r: 27
                    },
                    labelCfg: {
                        style: {
                            fill: this.node_color,
                        },
                    },
                    anchorPoints: [
                        [0, 0.25],
                        [0, 0.5],
                        [0, 0.75],
                        [1, 0.25],
                        [1, 0.5],
                        [1, 0.75],
                        [0.25, 0],
                        [0.5, 0],
                        [0.75, 0],
                        [0.25, 1],
                        [0.5, 1],
                        [0.75, 1]
                    ],
                },
                defaultEdge: {
                    type: "cubic-horizontal",
                    labelCfg: {
                        autoRotate: true,
                        refY: 10,
                        refX: 0,
                        style: {
                            fill: this.node_color
                        }
                    },
                    style: {
                        endArrow: {
                            path: G6.Arrow.triangle(),
                        },
                        stroke: this.edge_color,
                        lineAppendWidth: 40
                    },
                },
                modes: {
                    default: [{
                            type: 'drag-canvas',
                        }, {
                            type: 'drag-node',
                        },
                        {
                            type: 'zoom-canvas',
                            sensitivity: 0.5,
                        }, {
                            type: 'click-select',
                        }, {
                            type: 'brush-select',
                        },
                    ],
                    edit: [{
                        type: 'create-edge',
                        trigger: 'drag',
                    }, ],
                },
                plugins: [this.plug_minimap, this.plug_toolbar, this.plug_contextMenu],
                enabledStack: true,
            });

            this.graph.read(this.data);

            this.is_mounted++;

            this.onTheme();
            this.onFlowChartEvent();
        },
        onFlowChartEvent() {
            // 初始化流程图事件 

            this.graph.on("node:mouseout", (evt) => {
                // 鼠标移出节点，切换 default 模式，可拖拽节点和画布等操作
                this.graph.setMode('default');
            });

            this.graph.on("node:dblclick", (evt) => {
                // const item = evt.item; // 被操作的节点 item
                // const target = evt.target; // 被操作的具体图形
                // 鼠标双击节点，切换 edit 模式，可进行连线
                this.graph.setMode('edit');
            });

            this.graph.on("dragend", (evt) => {
                this.data = this.graph.save();
                this.onSave();
            });

            // this.graph.on('beforecreateedge', e => {
            //     console.log(this.graph.save())
            //     console.log(JSON.stringify(this.data));
            // })

            this.graph.on('aftercreateedge', e => {
                let edge_id = uuid.v1();

                this.graph.updateItem(e.edge, {
                    id: edge_id,
                    label: ""
                });

                this.onTheme();
                this.onSave();
            })
        },
        onTheme() {
            for (let index = 0; index < this.data.nodes.length; index++) {
                let node_id = this.data.nodes[index].id;
                let item = this.graph.findById(node_id);

                this.graph.updateItem(item, {
                    labelCfg: {
                        style: {
                            fill: this.node_color,
                        }
                    },
                });
            }

            for (let index = 0; index < this.data.edges.length; index++) {
                let edge_id = this.data.edges[index].id;
                let item = this.graph.findById(edge_id);

                if (edge_id.indexOf("edge") != -1) {
                    this.graph.removeItem(item)
                } else {
                    this.graph.updateItem(item, {
                        style: {
                            stroke: this.edge_color
                        },
                        labelCfg: {
                            style: {
                                fill: this.node_color
                            }
                        }
                    });
                }
            }

            this.onSave();
        },
        onShow(key, name, type) {
            this.key = key;

            if (type === "node") {
                this.app_info = this.app_data[key];

                var eidt_data = this.app_data[key]["data"];

                if (typeof eidt_data != "undefined") {
                    eidt_data["node_name"] = name;
                    this.selectAppAction(eidt_data.action)
                    setTimeout(() => {
                        this.form.setFieldsValue(eidt_data);
                    }, 100);
                } else {
                    this.app_info_args = [];

                    setTimeout(() => {
                        this.form.setFieldsValue({
                            "node_name": name,
                            "action": ""
                        });
                    }, 100);
                }
                this.visible_edit = true;
            } else if (type === "edge") {
                setTimeout(() => {
                    this.form_edge.setFieldsValue({
                        "ifelse": name
                    });
                }, 100);

                this.visible_edit_edge = true;
            }
        },
        onClose() {
            this.visible_edit = false;
        },
        onCloseEdge() {
            this.visible_edit_edge = false;
        },
        onShowLog() {
            this.visible_log = true;
        },
        onCloseLog() {
            this.execute_log = [];
            this.visible_log = false;
        },
        selectAppAction(action) {
            this.app_info_args = this.app_info["args"][action];
        },
        onUpdate(e) {
            e.preventDefault();

            this.form.validateFields((err, values) => {
                if (!err) {
                    this.app_data[this.key]["data"] = values;

                    this.graph.updateItem(this.graph.findById(this.key), {
                        "label": values.node_name
                    });

                    this.onSave();
                    this.onClose();
                }
            });
        },
        onUpdateEdge(e) {
            e.preventDefault();

            this.form_edge.validateFields((err, values) => {
                if (!err) {
                    this.graph.updateItem(this.graph.findById(this.key), {
                        "label": values.ifelse
                    });

                    this.onSave();
                    this.onCloseEdge();
                }
            });
        },
        initWebSocket() {
            //初始化weosocket
            var wsuri = this.BaseURL.replace("http", "ws").replace("https", "wss") + "/logs";
            // var wsuri = "ws://127.0.0.1:8081/logs";

            this.websock = new WebSocket(wsuri);

            // 连接成功
            this.websock.onopen = this.websocketOpen;

            // 连接异常
            this.websock.onerror = this.websocketError;

            // 接收消息
            this.websock.onmessage = this.websocketMSG;

            // 断开连接
            this.websock.onclose = this.websocketClose;
        },
        websocketPing() {
            this.ping_timetimer = setInterval(() => {

                let msg = JSON.stringify({
                    "method": "ping"
                })

                this.sendMSG(msg);
            }, 30000);
        },
        websocketOpen() {
            this.websocketPing();
            //连接建立之后执行send方法发送数据

            let msg = JSON.stringify({
                "method": "ping"
            })

            this.sendMSG(msg);
        },
        websocketError() {
            //连接建立失败重连
            this.initWebSocket();
        },
        websocketMSG(e) {
            var result = JSON.parse(e.data);

            if (result.method == "execute_log") {
                var data = result.data;
                this.execute_log.push(data);
            } else {
                this.$message.destroy();
                this.$message.error("长连接未知错误");
            }
        },
        websocketClose(e) {
            //关闭
            console.log("已经关闭了长连接");
        },
        sendMSG(data) {
            //数据发送
            this.websock.send(data);
        },
    },
    watch: {
        '$store.getters.getTheme': function (e) {
            if (this.is_mounted > 1) {
                this.onColor();
                this.onTheme();
            }
            // try {
            //     this.graph.destroy()
            // } catch (error) {
            //     console.log(error)
            // }

            // this.onFlowChart(1);
        }
    }
};
</script>

