<template>
<a-layout-sider v-model="collapsed" :trigger="null" collapsible>
    <div class="logo" v-if="collapsed">
        <a-avatar class="logo_imgs" :src="baseURL+'/app/basic/logo.png'" />
    </div>
    <div class="logo" v-else>
        <a-avatar class="logo_img" :src="baseURL+'/app/basic/logo.png'" />
        <span>织布鸟</span>
    </div>
    <a-menu theme="dark" mode="inline" :default-selected-keys="defaultSelectedKeys" @click="click">
        <a-menu-item key="Dashboard">
            <a-icon type="dashboard" />
            <span>仪表盘</span>
        </a-menu-item>
        <a-menu-item key="WorkflowHome">
            <a-icon type="cluster" />
            <span>Workflow</span>
        </a-menu-item>
        <a-menu-item key="LogsHome">
            <a-icon type="bug" />
            <span>剧本日记</span>
        </a-menu-item>
        <a-menu-item key="AppHome">
            <a-icon type="appstore" />
            <span>应用中心</span>
        </a-menu-item>
        <!-- <a-menu-item key="VariablenHome">
            <a-icon type="coffee" />
            <span>全局变量</span>
        </a-menu-item> -->
        <a-menu-item key="ClassificationHome">
            <a-icon type="gateway" />
            <span>分类管理</span>
        </a-menu-item>
        <a-menu-item key="UserHome">
            <a-icon type="team" />
            <span>用户管理</span>
        </a-menu-item>
        <!-- <a-menu-item key="SystemHome">
            <a-icon type="setting" />
            <span>角色管理</span>
        </a-menu-item>
        <a-menu-item key="SystemHome">
            <a-icon type="setting" />
            <span>系统管理</span>
        </a-menu-item> -->
    </a-menu>

    <div class="nav_footer" v-if="collapsed">
        <div>v0.1</div>
    </div>
    <div class="nav_footer" v-else>
        <div>Copyright © ZBN SOAR v0.1</div>
    </div>

</a-layout-sider>
</template>

<script>
export default {
    name: "MyNav",
    data() {
        return {
            baseURL: this.BaseURL,
            defaultSelectedKeys: ["Dashboard"],
        };
    },
    computed: {
        collapsed() {
            return this.$store.getters.getCollapsed;
        },
    },
    created() {
        this.setDefaultSelectedKeys();
    },
    mounted() {
        this.setDefaultSelectedKeys();
    },
    methods: {
        setDefaultSelectedKeys() {
            var key = this.$router.history.current.name;

            if (key === "WorkflowEdit") {
                this.defaultSelectedKeys = ["WorkflowHome"];
            } else {
                this.defaultSelectedKeys = [key];
            }
        },
        click(e) {
            this.$router.push({
                name: e.key,
            });
        },
    }
};
</script>

<style lang="less" scoped>
@import url("../theme/style.less");

.ant-menu-dark {
    background: inherit;
}

.blue {
    .logo {
        margin: 16px;
        text-align: center;
        color: @text-color1;
        font-size: 20px;

        .logo_img {
            width: 30px;
            height: 30px;
            margin-right: 5px;
            vertical-align: bottom;
        }

        .logo_imgs {
            width: 30px;
            height: 30px;
        }
    }

    .ant-layout-sider {
        background: @nav-bg-color;
    }

    .ant-menu.ant-menu-dark .ant-menu-item-selected {
        background-color: @nav-bg-color1;
    }
}

.dark,
.dark1 {
    .logo {
        margin: 16px;
        text-align: center;
        color: @jin-color;
        font-size: 20px;

        .logo_img {
            width: 30px;
            height: 30px;
            margin-right: 5px;
            vertical-align: bottom;
        }

        .logo_imgs {
            width: 30px;
            height: 30px;
        }
    }

    .ant-layout-sider {
        background: @dark-nav-bg-color;
        border-right: @border;
    }

    .ant-menu.ant-menu-dark .ant-menu-item-selected {
        background-color: inherit;
        border-right: 2px solid @dark-nav-bg-color1;
    }

    .ant-menu-dark .ant-menu-item-selected .anticon {
        color: @dark-nav-bg-color1;
    }
}

.nav_footer {
    color: #a0a0a0;
    text-align: center;
    position: absolute;
    bottom: 10px;
    text-align: center;
    width: 100%;
    font-size: 12px;
}
</style>



