<template>
  <a-layout-header class="headerx">
    <a-icon class="trigger" :type="collapsed ? 'menu-unfold' : 'menu-fold'" @click="collapsedClick"/>

    <div class="userinfo">
      <a-space :size="space_size">
        <a-tooltip placement="bottom">
          <template slot="title">
            <span>帮助文档</span>
          </template>
          <a href="https://www.yuque.com/zbn/docs/ayilv9" target="_bank">
            <a-icon type="question-circle"/>
          </a>
        </a-tooltip>

        <a-tooltip placement="bottom">
          <template slot="title">
            <span>Github</span>
          </template>
          <a href="https://github.com/zbnio/zbn" target="_bank">
            <a-icon type="github" theme="filled"/>
          </a>
        </a-tooltip>

        <a-dropdown>
          <a class="ant-dropdown-link" @click="(e) => e.preventDefault()">
            <a-icon type="skin"/>
            <a-icon class="icon" type="down"/>
          </a>
          <a-menu slot="overlay">
            <a-menu-item v-if="curr_theme == 'dark'">
              <a href="javascript:;" @click="setTheme('dark')">暗色主题</a>
            </a-menu-item>
            <a-menu-item v-else>
              <a href="javascript:;" @click="setTheme('dark')">暗色主题</a>
            </a-menu-item>

            <a-menu-item v-if="curr_theme == 'dark1'">
              <a href="javascript:;" @click="setTheme('dark1')">黑金亮色</a>
            </a-menu-item>
            <a-menu-item v-else>
              <a href="javascript:;" @click="setTheme('dark1')">黑金亮色</a>
            </a-menu-item>

            <a-menu-item v-if="curr_theme == 'blue'">
              <a href="javascript:;" @click="setTheme('blue')">蓝色亮色</a>
            </a-menu-item>
            <a-menu-item v-else>
              <a href="javascript:;" @click="setTheme('blue')">蓝色亮色</a>
            </a-menu-item>

          </a-menu>
        </a-dropdown>

        <a-dropdown>
          <a class="ant-dropdown-link" @click="(e) => e.preventDefault()">
            <a-avatar class="avatar" size="small" :src="baseURL+'/app/basic/logo.png'"/>

            <span class="name">{{ nick_name }}
                        <a-icon class="icon" type="down"/></span>
          </a>
          <a-menu slot="overlay">
            <!-- <a-menu-item>
      <a href="javascript:;"> <a-icon type="user" /> 个人信息</a>
    </a-menu-item>
    <a-menu-divider /> -->
            <a-menu-item>
              <a href="javascript:;" @click="logout">
                <a-icon type="poweroff"/>
                退出</a>
            </a-menu-item>
          </a-menu>
        </a-dropdown>
      </a-space>
    </div>
  </a-layout-header>
</template>

<script>
export default {
  name: "MyHeader",
  data() {
    return {
      space_size: 18,
      curr_theme: "dark",
      nick_name: "",
      baseURL: this.BaseURL,
    };
  },
  computed: {
    collapsed: {
      get: function () {
        return this.$store.getters.getCollapsed;
      },
      set: function (val) {
        if (val) {
          this.$store.commit("closeCollapsed");
        } else {
          this.$store.commit("openCollapsed");
        }
      },
    },
  },
  mounted() {
    this.getTheme();
    this.getUserName();
  },
  methods: {
    getUserName() {
      this.nick_name = this.$cookies.get("nick_name");
    },
    getTheme() {
      var is_theme = this.$cookies.isKey("theme");
      if (is_theme) {
        var theme = this.$cookies.get("theme");
        document.getElementById("app").className = theme;
        this.curr_theme = theme;
        this.$store.commit("setTheme", this.curr_theme);
      } else {
        document.getElementById("app").className = "dark";
        this.curr_theme = "dark";
        this.$store.commit("setTheme", this.curr_theme);
      }
    },
    setTheme(name) {
      document.getElementById("app").className = name;
      this.$cookies.set("theme", name, -1);
      this.curr_theme = name;
      this.$store.commit("setTheme", this.curr_theme);
    },
    collapsedClick() {
      if (this.collapsed) {
        this.collapsed = false;
      } else {
        this.collapsed = true;
      }
    },
    logout() {
      this.$cookies.remove("token");
      this.$cookies.remove("nick_name");
      this.$cookies.remove("account");
      this.$cookies.remove("user_id");

      window.location.href = "/";
    },
  },
};
</script>

<style lang="less" scoped>
@import url("../theme/style.less");

.blue,
.dark1 {
  .headerx {
    background: @background;
    padding: 0;
    border: 0px;
    box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.05);
    height: 50px;
    line-height: 45px;

    .trigger {
      font-size: 18px;
      line-height: 60px;
      padding: 0 15px;
      cursor: pointer;
      transition: color 0.3s;
    }

    .trigger:hover {
      color: @a-color;
    }

    .userinfo {
      float: right;
      margin-right: 18px;

      a {
        color: @text-color;
      }

      .name {
        color: @text-color;
        vertical-align: middle;
        margin-left: 3px;
      }

      .icon {
        font-size: 10px;
      }
    }
  }
}

.dark {
  .headerx {
    background: @dark-background;
    padding: 0;
    border: 0px;
    box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.05);
    height: 50px;
    line-height: 45px;

    .trigger {
      font-size: 18px;
      line-height: 60px;
      padding: 0 15px;
      cursor: pointer;
      transition: color 0.3s;
      color: @dark-text-color;
    }

    .trigger:hover {
      color: @dark-text-color1;
    }

    .userinfo {
      float: right;
      margin-right: 18px;

      a {
        color: @dark-text-color;
      }

      .name {
        color: @dark-text-color;
        vertical-align: middle;
        margin-left: 3px;
      }

      .icon {
        font-size: 10px;
      }
    }
  }
}
</style>



