<template>
<a-layout-content>
    <a-row :gutter="[16,16]">
        <a-col :span="8">
            <a-card class="icondiv">
                <div class="left">
                    <a-icon class="icons" type="cluster" />
                    <span class="namex">剧本数量</span>
                </div>
                <div class="right">{{sums_data['workflow_count']}}</div>
            </a-card>
        </a-col>
        <a-col :span="8">
            <a-card class="icondiv">
                <div class="left">
                    <a-icon class="icons" type="car" />
                    <span class="namex">执行次数</span>
                </div>
                <div class="right">{{sums_data['logs_count']}}</div>
            </a-card>
        </a-col>
        <a-col :span="8">
            <a-card class="icondiv">
                <div class="left">
                    <a-icon class="icons" type="team" />
                    <span class="namex">用户数量</span>
                </div>
                <div class="right">{{sums_data['user_count']}}</div>
            </a-card>
        </a-col>
        <a-col :span="8">
            <a-card size="small">
                <span slot="title" class="titlex">
                    <a-icon type="pie-chart" /> 剧本分布</span>
                <div class="tb_div" id="main1">
                </div>
            </a-card>
        </a-col>
        <a-col :span="16">
            <a-card size="small">
                <span slot="title" class="titlex">
                    <a-icon type="line-chart" /> 执行统计</span>
                <div class="tb_div" id="main2">

                </div>
            </a-card>
        </a-col>

        <a-col :span="24">
            <a-card size="small">
                <span slot="title" class="titlex">
                    <a-icon type="bug" /> 剧本日记</span>
                <pre class="log_div">
        <p v-for="(item,index) in logs_data" :key="index"><span class="name">[{{item.name}}]</span> <span class="heng">-</span> <span>{{item.uuid}}</span> <span class="heng">-</span> <span class="appname">[{{item.app_name}}]</span> <span class="heng">-</span> <span class="appresult">{{item.result}}</span> <span class="heng">-</span> <span class="time">{{item.create_time.replace("T", " ")}}</span></p>
    </pre>
            </a-card>
        </a-col>

    </a-row>

</a-layout-content>
</template>

<script>
import {
    Area,
    Pie
} from '@antv/g2plot';

export default {
    name: 'dashboard',
    data() {
        return {
            sums_data: {},
            logs_data: [],
            workflow_data: [],
            exec_data: [],
            theme: "dark"
        }
    },
    mounted() {
        this.onLoad();
    },
    methods: {
        onLoad() {
            this.onLoadSums();
            this.onLoadLogs();
            this.onLoadWorkflow();
            this.onLoadExec();
        },
        gd() {
            this.$router.push({
                name: "LogsHome"
            });
        },
        onLoadSums() {
            this.$http
                .post("/api/v1/get/dashboard/sums")
                .then((res) => {
                    if (res.code == 0) {
                        this.sums_data = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onLoadLogs() {
            this.$http
                .post("/api/v1/get/dashboard/logs")
                .then((res) => {
                    if (res.code == 0) {
                        this.logs_data = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onLoadWorkflow() {
            this.$http
                .post("/api/v1/get/dashboard/workflow")
                .then((res) => {
                    if (res.code == 0) {
                        this.workflow_data = res.data;
                        this.onLoadMain1();
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onLoadExec() {
            this.$http
                .post("/api/v1/get/dashboard/exec")
                .then((res) => {
                    if (res.code == 0) {
                        this.exec_data = res.data;
                        this.onLoadMain2();
                    } else {
                        this.$message.error(res.msg);
                    }
                });
        },
        onLoadMain1() {
            if (this.theme === "blue" || this.theme === "dark1") {
                let piePlot = new Pie('main1', {
                    appendPadding: 10,
                    data: this.workflow_data,
                    angleField: 'value',
                    colorField: 'type',
                    radius: 0.8,
                    label: {
                        type: 'outer',
                        content: '{name}\n{percentage}',
                        style: {
                            fill: '#000',
                        },
                    },
                    legend: false,
                    interactions: [{
                        type: 'element-active'
                    }],
                });

                piePlot.render();
            } else if (this.theme === "dark") {
                let piePlot = new Pie('main1', {
                    appendPadding: 10,
                    data: this.workflow_data,
                    angleField: 'value',
                    colorField: 'type',
                    radius: 0.8,
                    label: {
                        type: 'outer',
                        content: '{name}\n{percentage}',
                        style: {
                            fill: '#ccc',
                        },
                    },
                    legend: false,
                    interactions: [{
                        type: 'element-active'
                    }],
                });

                piePlot.render();
            }
        },
        onLoadMain2() {
            let areaPlot = new Area('main2', {
                data: this.exec_data,
                xField: 'timex',
                yField: 'value',
            });
            areaPlot.render();
        }
    },
    watch: {
        '$store.getters.getTheme': function (e) {
            this.theme = this.$store.getters.getTheme;
        }
    }
}
</script>

<style lang="less" scoped>
.tb_div {
    height: 250px;
}

.icondiv {
    .left {
        float: left;

        .icons {
            font-size: 20px;
            vertical-align: middle;
        }

        .namex {
            margin-left: 5px;
            font-size: 18px;
            vertical-align: middle;
        }
    }

    .right {
        float: right;
        font-size: 18px;
        font-weight: bold;
    }
}

.log_div {
    background: #151719;
    color: #bbb;
    height: 180px;
    margin: -10px;
    border-radius: 5px;
    padding-left: 30px;
    line-height: 15px;
    font-size: 14px;
    overflow-y: auto;
    margin-top: 0px;
}

.blue,
.dark1 {
    .heng {
        color: #636363;
        margin-left: 15px;
        margin-right: 15px;
    }

    .name {
        color: #61b0fd;
    }

    .appresult {
        color: #ffffff;
    }
}

.dark {
    .heng {
        color: #636363;
        margin-left: 15px;
        margin-right: 15px;
    }

    .name {
        color: #d9a066;
    }

    .appresult {
        color: #ffffff;
    }

    .ant-card {
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        font-size: 14px;
        font-variant: tabular-nums;
        line-height: 1.5;
        list-style: none;
        -webkit-font-feature-settings: 'tnum';
        font-feature-settings: 'tnum';
        position: relative;
        border-radius: 2px;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
        border: 0px;
        border-radius: 5px;
        background: #202225;
        margin-bottom: 10px;
        padding: 10px;
        border: 1px solid #353535;
        color: #cacaca;

        /deep/.ant-card-head {
            border-bottom: 1px solid #353535;
        }

    }

    .titlex {
        color: #cacaca;
    }
}
</style>



