import Vue from "vue";
import Router from "vue-router";
import Login from "@/pages/login";
import Dashboard from "@/pages/dashboard";
import WorkflowHome from "@/pages/workflow/home";
import WorkflowEdit from "@/pages/workflow/edit";
import LogsHome from "@/pages/logs/home";
import AppHome from "@/pages/app/home";
import ClassificationHome from "@/pages/classification/home";
import VariablenHome from "@/pages/variable/home";
import UserHome from "@/pages/user/home";
import SystemHome from "@/pages/system/home";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Login",
      component: Login,
      meta: {requireAuth: false}
    },
    {
      path: "/dashboard",
      name: "Dashboard",
      component: Dashboard,
      meta: {requireAuth: true}
    },
    {
      path: "/workflow",
      name: "WorkflowHome",
      component: WorkflowHome,
      meta: {requireAuth: true}
    },
    {
      path: "/workflow/edit/:uuid",
      name: "WorkflowEdit",
      component: WorkflowEdit,
      meta: {requireAuth: true}
    },
    {
      path: "/logs",
      name: "LogsHome",
      component: LogsHome,
      meta: {requireAuth: true}
    },
    {
      path: "/app",
      name: "AppHome",
      component: AppHome,
      meta: {requireAuth: true}
    },
    {
      path: "/classification",
      name: "ClassificationHome",
      component: ClassificationHome,
      meta: {requireAuth: true}
    },
    {
      path: "/variablen",
      name: "VariablenHome",
      component: VariablenHome,
      meta: {requireAuth: true}
    },
    {
      path: "/user",
      name: "UserHome",
      component: UserHome,
      meta: {requireAuth: true}
    },
    {
      path: "/system",
      name: "SystemHome",
      component: SystemHome,
      meta: {requireAuth: true}
    }
  ]
});



